"use client"

import { motion } from "framer-motion"
import { Section } from "@/components/ui/section"
import { Star, Quote } from "lucide-react"

const testimonials = [
    {
        quote: "The attention to detail and futuristic design language completely transformed our brand identity. Highly recommended!",
        author: "Sarah Johnson",
        role: "CEO, Nexus Tech",
        rating: 5,
    },
    {
        quote: "Incredible work ethic and transparent communication throughout the entire process. The final product exceeded our expectations.",
        author: "David Chen",
        role: "Founder, Orbit AI",
        rating: 5,
    },
    {
        quote: "Not just a developer, but a true partner. The strategic insights provided were just as valuable as the code itself.",
        author: "Elena Rodriguez",
        role: "CTO, Quantum Leap",
        rating: 5,
    },
]

export function TestimonialsSection() {
    return (
        <Section className="py-24 relative overflow-hidden">
            <div className="container mx-auto px-4">
                <motion.div
                    initial={{ opacity: 0, y: 30 }}
                    whileInView={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.6 }}
                    viewport={{ once: true }}
                    className="text-center mb-20"
                >
                    <span className="text-[#00D9FF] font-medium tracking-wider uppercase text-sm mb-4 block">
                        Client Feedback
                    </span>
                    <h2 className="font-heading text-4xl lg:text-5xl font-bold text-foreground mb-6">
                        Why Clients <span className="text-transparent bg-clip-text bg-gradient-to-r from-[#00D9FF] to-[#7B3FEF]">Choose Me</span>
                    </h2>
                    <p className="text-muted-foreground text-lg max-w-2xl mx-auto">
                        Don't just take my word for it. Here's what early adopters and partners have to say.
                    </p>
                </motion.div>

                <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
                    {testimonials.map((item, index) => (
                        <motion.div
                            key={index}
                            initial={{ opacity: 0, y: 20 }}
                            whileInView={{ opacity: 1, y: 0 }}
                            transition={{ duration: 0.5, delay: index * 0.1 }}
                            viewport={{ once: true }}
                            className="bg-card/30 backdrop-blur-sm border border-white/5 p-8 rounded-3xl relative group hover:bg-card/50 transition-colors"
                        >
                            <Quote className="absolute top-8 right-8 text-white/5 w-12 h-12 rotate-180" />

                            <div className="flex gap-1 mb-6">
                                {[...Array(item.rating)].map((_, i) => (
                                    <Star key={i} size={16} className="fill-[#FFD700] text-[#FFD700]" />
                                ))}
                            </div>

                            <p className="text-muted-foreground leading-relaxed mb-8 relative z-10">
                                "{item.quote}"
                            </p>

                            <div className="flex items-center gap-4">
                                <div className="w-10 h-10 rounded-full bg-gradient-to-br from-[#00D9FF] to-[#7B3FEF] flex items-center justify-center text-white font-bold text-sm">
                                    {item.author.charAt(0)}
                                </div>
                                <div>
                                    <div className="font-bold text-foreground">{item.author}</div>
                                    <div className="text-xs text-muted-foreground">{item.role}</div>
                                </div>
                            </div>
                        </motion.div>
                    ))}
                </div>
            </div>
        </Section>
    )
}
