"use client"

import { motion } from "framer-motion"
import { Section } from "@/components/ui/section"

const achievements = [
    {
        value: "500+",
        label: "Projects Delivered",
        description: "Successful digital solutions deployed worldwide",
        color: "#7B3FEF",
    },
    {
        value: "98%",
        label: "Client Satisfaction",
        description: "Based on post-project feedback surveys",
        color: "#00D9FF",
    },
    {
        value: "50+",
        label: "Team Members",
        description: "Experts in design, development, and strategy",
        color: "#FF0080",
    },
    {
        value: "15+",
        label: "Years in Business",
        description: "Consistent growth and innovation since 2010",
        color: "#FFD700",
    },
    {
        value: "25+",
        label: "Countries Served",
        description: "Global impact across multiple continents",
        color: "#00FF94",
    },
    {
        value: "100+",
        label: "Industry Awards",
        description: "Recognition for design and technical excellence",
        color: "#FF4D4D",
    },
]

export function AchievementsSection() {
    return (
        <Section className="py-20 relative overflow-hidden bg-black/40">
            {/* Background Grid */}
            <div className="absolute inset-0 bg-[linear-gradient(to_right,#80808012_1px,transparent_1px),linear-gradient(to_bottom,#80808012_1px,transparent_1px)] bg-[size:24px_24px]" />

            <div className="container mx-auto px-4 relative z-10">
                <motion.div
                    initial={{ opacity: 0, y: 30 }}
                    whileInView={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.6 }}
                    viewport={{ once: true }}
                    className="text-center mb-16"
                >
                    <h2 className="font-heading text-4xl lg:text-5xl font-bold text-white mb-4">
                        Our <span className="text-transparent bg-clip-text bg-gradient-to-r from-[#00D9FF] to-[#7B3FEF]">Achievements</span>
                    </h2>
                    <div className="h-1 w-24 mx-auto my-6 bg-gradient-to-r from-transparent via-[#7B3FEF] to-transparent rounded-full" />
                    <p className="text-muted-foreground text-lg max-w-2xl mx-auto">
                        Numbers that reflect our commitment to excellence and client success.
                    </p>
                </motion.div>

                <div className="grid grid-cols-2 lg:grid-cols-3 gap-6 lg:gap-8">
                    {achievements.map((item, index) => (
                        <motion.div
                            key={index}
                            initial={{ opacity: 0, scale: 0.9 }}
                            whileInView={{ opacity: 1, scale: 1 }}
                            transition={{ duration: 0.5, delay: index * 0.1 }}
                            viewport={{ once: true }}
                            className="relative group p-6 rounded-2xl bg-card/30 border border-white/5 hover:border-white/10 transition-all duration-300 hover:bg-card/50"
                        >
                            <div className="text-center">
                                <div
                                    className="font-heading text-4xl lg:text-5xl font-bold mb-2 bg-clip-text text-transparent bg-gradient-to-r from-white to-white/70 group-hover:to-white transition-all"
                                    style={{
                                        textShadow: `0 0 20px ${item.color}40`,
                                        backgroundImage: `linear-gradient(135deg, ${item.color}, #fff)`
                                    }}
                                >
                                    {item.value}
                                </div>
                                <div className="text-sm font-semibold text-white mb-2">{item.label}</div>
                                <div className="text-xs text-muted-foreground">{item.description}</div>
                            </div>

                            {/* Corner Accents */}
                            <div className="absolute top-0 left-0 w-2 h-2 border-t border-l border-white/20 rounded-tl-lg" />
                            <div className="absolute top-0 right-0 w-2 h-2 border-t border-r border-white/20 rounded-tr-lg" />
                            <div className="absolute bottom-0 left-0 w-2 h-2 border-b border-l border-white/20 rounded-bl-lg" />
                            <div className="absolute bottom-0 right-0 w-2 h-2 border-b border-r border-white/20 rounded-br-lg" />
                        </motion.div>
                    ))}
                </div>
            </div>
        </Section>
    )
}
