"use client"

import { motion } from "framer-motion"
import Link from "next/link"
import { ArrowRight, ExternalLink } from "lucide-react"
import { Section } from "@/components/ui/section"

interface CaseStudy {
    title: string
    institute: string
    description: string
    stats: { value: string; label: string }[]
    image: string
    category: string
    href: string
    color: string
}

const caseStudies: CaseStudy[] = [
    {
        title: "Healthcare AI Diagnostics",
        institute: "Medical Research Institute",
        description: "Developed an AI-powered diagnostic system for early disease detection using deep learning models.",
        stats: [
            { value: "94%", label: "Accuracy Rate" },
            { value: "70%", label: "Faster Diagnosis" },
            { value: "50K+", label: "Patients Helped" },
        ],
        image: "/ai-analytics-dashboard.png",
        category: "AI & Machine Learning",
        href: "/case-studies/healthcare-ai",
        color: "#7B3FEF",
    },
    {
        title: "Smart Manufacturing IoT",
        institute: "Industrial Manufacturing Corp",
        description: "Implemented IoT sensors and a predictive maintenance system across manufacturing facilities.",
        stats: [
            { value: "45%", label: "Downtime Reduction" },
            { value: "$2M", label: "Annual Savings" },
            { value: "85%", label: "Efficiency Gain" },
        ],
        image: "/neon-ecommerce-website.jpg",
        category: "IoT Solutions",
        href: "/case-studies/smart-manufacturing",
        color: "#00D9FF",
    },
    {
        title: "FinTech Revolution",
        institute: "Global Bank Inc.",
        description: "Engineered a secure, scalable, and high-performance trading platform, increasing transaction speed by 300%.",
        stats: [
            { value: "300%", label: "Faster Transactions" },
            { value: "99.9%", label: "Uptime" },
            { value: "10M+", label: "Daily Transactions" },
        ],
        image: "/fintech-dashboard-ui.jpg",
        category: "Financial Technology",
        href: "/case-studies/fintech-revolution",
        color: "#7B3FEF",
    },
]

export function FeaturedProjectsSection() {
    return (
        <Section className="bg-gradient-to-b from-muted/20 to-background relative overflow-hidden">
            {/* Background Elements */}
            <div className="absolute inset-0 bg-[url('/grid-pattern.svg')] opacity-5" />
            <div className="absolute top-0 left-0 w-full h-px bg-gradient-to-r from-transparent via-[#00D9FF]/20 to-transparent" />

            <div className="container mx-auto px-4 relative z-10">
                <motion.div
                    initial={{ opacity: 0, y: 30 }}
                    whileInView={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.6 }}
                    viewport={{ once: true }}
                    className="text-center mb-16"
                >
                    <div className="inline-block mb-6">
                        <span className="px-4 py-2 rounded-full bg-gradient-to-r from-[#00D9FF]/10 to-[#7B3FEF]/10 border border-[#00D9FF]/20 text-sm font-medium text-[#00D9FF] shadow-[0_0_15px_rgba(0,217,255,0.3)]">
                            Success Stories
                        </span>
                    </div>
                    <h2 className="font-heading text-4xl lg:text-5xl font-bold text-foreground mb-4 tracking-tight">
                        Featured <span className="text-transparent bg-clip-text bg-gradient-to-r from-[#00D9FF] to-[#7B3FEF]">Projects</span>
                    </h2>
                    <div className="h-1 w-24 mx-auto my-6 bg-gradient-to-r from-transparent via-[#00D9FF] to-transparent rounded-full" />
                </motion.div>

                <div className="grid grid-cols-1 lg:grid-cols-2 gap-8 lg:gap-12">
                    {caseStudies.map((study, index) => (
                        <motion.div
                            key={index}
                            initial={{ opacity: 0, y: 30 }}
                            whileInView={{ opacity: 1, y: 0 }}
                            transition={{ duration: 0.6, delay: index * 0.1 }}
                            viewport={{ once: true }}
                            className="group relative bg-card/50 backdrop-blur-sm border border-border/50 rounded-3xl overflow-hidden hover:border-[#00D9FF]/50 transition-all duration-500"
                        >
                            {/* Image Section */}
                            <div className="relative h-64 lg:h-80 overflow-hidden">
                                <div className="absolute inset-0 bg-gradient-to-t from-background via-transparent to-transparent z-10" />
                                <img
                                    src={study.image || "/placeholder.svg"}
                                    alt={study.title}
                                    className="w-full h-full object-cover transition-transform duration-700 group-hover:scale-110"
                                />
                                <div className="absolute top-4 left-4 z-20">
                                    <span className="px-4 py-1.5 rounded-full text-xs font-semibold bg-background/80 backdrop-blur-md border border-white/10 text-white shadow-lg">
                                        {study.category}
                                    </span>
                                </div>
                            </div>

                            {/* Content Section */}
                            <div className="p-8 relative z-20 -mt-12">
                                <div className="bg-card/90 backdrop-blur-xl border border-white/5 rounded-2xl p-6 shadow-xl">
                                    <div className="mb-4">
                                        <p className="text-sm text-[#00D9FF] font-medium mb-1">{study.institute}</p>
                                        <h3 className="font-heading text-2xl font-bold text-white mb-3 group-hover:text-[#00D9FF] transition-colors">
                                            {study.title}
                                        </h3>
                                        <p className="text-muted-foreground text-sm leading-relaxed mb-6">
                                            {study.description}
                                        </p>
                                    </div>

                                    {/* Stats Grid */}
                                    <div className="grid grid-cols-3 gap-4 mb-6 py-4 border-y border-white/5">
                                        {study.stats.map((stat, i) => (
                                            <div key={i} className="text-center">
                                                <div className="text-lg lg:text-xl font-bold text-white mb-1" style={{ color: study.color }}>
                                                    {stat.value}
                                                </div>
                                                <div className="text-[10px] uppercase tracking-wider text-muted-foreground">
                                                    {stat.label}
                                                </div>
                                            </div>
                                        ))}
                                    </div>

                                    <Link
                                        href={study.href}
                                        className="inline-flex items-center gap-2 text-sm font-medium text-white hover:text-[#00D9FF] transition-colors group/link"
                                    >
                                        Read Full Case Study
                                        <ArrowRight className="w-4 h-4 transition-transform duration-300 group-hover/link:translate-x-1" />
                                    </Link>
                                </div>
                            </div>

                            {/* Hover Glow Effect */}
                            <div
                                className="absolute -inset-1 bg-gradient-to-r from-[#00D9FF] to-[#7B3FEF] rounded-3xl opacity-0 group-hover:opacity-20 blur-xl transition-opacity duration-500 -z-10"
                            />
                        </motion.div>
                    ))}
                </div>

                <div className="text-center mt-16">
                    <Link
                        href="/case-studies"
                        className="inline-flex items-center gap-2 px-8 py-4 rounded-full bg-gradient-to-r from-[#00D9FF]/10 to-[#7B3FEF]/10 border border-[#00D9FF]/20 text-[#00D9FF] font-medium hover:bg-[#00D9FF] hover:text-white transition-all duration-300 hover:shadow-[0_0_20px_rgba(0,217,255,0.4)]"
                    >
                        View All Projects
                        <ArrowRight size={20} />
                    </Link>
                </div>
            </div>
        </Section>
    )
}
